$("._toggle-menu").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$(document).mouseup( function(e){
    let div = $( ".mob-menu" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(".header").hasClass("_open")){
            $(".header").removeClass("_open")
            $("body").removeClass("fixed-body")
        }
    }
});

$(".lang__show").click(function () {
    $(".lang").toggleClass("_open")
})

$(document).mouseup( function(e){
    let div = $( ".lang" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});

$(".main-slide").click(function () {
    $(".lang").removeClass("_open")
})
