$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$("[data-copy]").click(function () {
    let text = $(this).attr("data-copy")
    let $tmp = $("<textarea>");
    $("body").append($tmp);
    $tmp.val(text).select();
    document.execCommand("copy");
    $tmp.remove();
})

$(".qa-card__show").click(function () {
    let parent = $(this).parents(".qa-card")
    $(parent).toggleClass("_open")
    $(parent).find(".qa-card__hidden").slideToggle()
})

if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
    console.log("dark mode")
}

function setTheme() {
    let light = localStorage.getItem("light")
    if (light === "true") {
        $("html").attr("data-theme", "light")
    } else {
        $("html").attr("data-theme", "dark")
    }
}

$(".theme-btn").click(function() {
    let light = localStorage.getItem("light")
    if (light === "true") {
        localStorage.setItem("light", "false")
    } else {
        localStorage.setItem("light", "true")
    }
    setTheme()
})

setTheme()
