$(".filter-item").each(function (i,el) {
    $(el).find(".filter-option input").change(function () {
        $(el).find(".filter-item__current").html(
            $(el).find(".filter-option input:checked").attr("data-value")
        )


        $(el).removeClass("_open")
    })

    $(el).find(".filter-item__show").click(function () {
        if($(el).hasClass("_open")){
            $(".filter-item").removeClass("_open")
        } else{
            $(".filter-item").removeClass("_open")
            $(el).addClass("_open")
        }

    })
})





$(document).mouseup( function(e){
    let div = $( ".filter-item" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});
